import axios, { AxiosRequestConfig } from 'axios';

// Base API configuration
const API = axios.create({
  baseURL: process.env.REACT_APP_API_URL || '/api',
  timeout: 30000,
  headers: {
    'Content-Type': 'application/json',
  },
});

// Types
export interface ApiResponse<T> {
  data: T;
  success: boolean;
  message?: string;
}

export interface Product {
  barcode: string;
  name: string;
  brand: string;
  ingredients: string[];
  countries: string;
  ingredient_image_url?: string;
  source_db?: string;
}

export interface OCRResult {
  ingredients: string[];
  processing_time: number;
  image_path: string;
}

// API Service
export const ApiService = {
  // Barcode lookup
  lookupBarcode: async (barcode: string): Promise<ApiResponse<Product>> => {
    try {
      const response = await API.post('/search', { barcode });
      return { data: response.data, success: true };
    } catch (error) {
      return { data: {} as Product, success: false, message: 'Failed to lookup barcode' };
    }
  },

  // OCR processing
  processImage: async (file: File): Promise<ApiResponse<OCRResult>> => {
    try {
      const formData = new FormData();
      formData.append('file', file);

      const config: AxiosRequestConfig = {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      };

      const response = await API.post('/ocr', formData, config);
      return { data: response.data, success: true };
    } catch (error) {
      return { 
        data: { ingredients: [], processing_time: 0, image_path: '' }, 
        success: false, 
        message: 'Failed to process image' 
      };
    }
  },

  // Process image from camera
  processCameraImage: async (blob: Blob): Promise<ApiResponse<OCRResult>> => {
    try {
      const formData = new FormData();
      formData.append('file', blob, 'camera-capture.jpg');

      const config: AxiosRequestConfig = {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      };

      const response = await API.post('/ocr', formData, config);
      return { data: response.data, success: true };
    } catch (error) {
      return { 
        data: { ingredients: [], processing_time: 0, image_path: '' }, 
        success: false, 
        message: 'Failed to process camera image' 
      };
    }
  },

  // Manual product entry
  addManualProduct: async (product: Partial<Product>): Promise<ApiResponse<Product>> => {
    try {
      const response = await API.post('/product/manual', product);
      return { data: response.data, success: true };
    } catch (error) {
      return { data: {} as Product, success: false, message: 'Failed to add product' };
    }
  },

  // Health check
  healthCheck: async (): Promise<boolean> => {
    try {
      const response = await API.get('/health');
      return response.status === 200;
    } catch (error) {
      return false;
    }
  }
};

export default ApiService;
